@import url(https://fonts.googleapis.com/css?family=Pacifico);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

#TitleBar {
  width: 100%;
  min-height: 10vh;
  background-color: white;
}
#TitleBar h2 {
  font-family: Pacifico;
  font-size: 140%;
}
@media (min-width: 600px) {
  #TitleBar h2 {
    font-size: 200%;
  }
}
#TitleBar .title-icon {
  font-size: 140%;
}
@media (min-width: 600px) {
  #TitleBar .title-icon {
    font-size: 200%;
  }
}

.post-image-wrapper {
  width: 75%;
}

.post-wrapper {
  width: 80%;
}

#Author-Description {
  padding-left: 1.3rem;
}

.author-post-img-wrapper {
  width: 10%;
}

#Author-Post-Header {
  padding-left: 0.7rem;
}

.post-author-username {
  font-size: 70%;
}

.author-description-comment {
  font-size: 80%;
}

.fixed-profile-row {
  z-index: 1;
}

#ProfileTitleBar {
  width: 100%;
  min-height: 10vh;
  background-color: white;
}
#ProfileTitleBar h2 {
  font-size: 85%;
}

.ProfileImageWrapper {
  width: 100%;
  height: 350px;
  width: 100%;
}
.CreatePostImageWrapper {
  width: 100%;
  height: 100%;
  width: 100%;
}

.profile-col-pad {
  padding: 0.07rem;
}

.profile-header-text {
  font-size: 220%;
}

#Author-Description {
  padding-left: 1.3rem;
}

.menu {
  position: fixed;
  z-index: 2;
}

#menu-items li {
  width: 100%;
  padding: 0px;
  min-height: 4rem;
  list-style: none;
  color: black;
}
#menu-items li:hover {
  cursor: pointer;
}

.hover-pointer:hover {
  cursor: pointer;
}

#CreatePostWrapper {
  min-height: 100vh;
}

.create-post-panel {
  min-height: 100vh;
  padding: 2rem;
}
.create-post-panel ::-webkit-scrollbar {
  width: 0px;
  background: transparent;
}

